.ace__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 6rem;
    position: fixed;
    z-index: 20;
    background-color: var(--color-bg);
    top: 0;
    right: 0;
    left: 0;
}

.ace__navbar img {
